import React from 'react';

function Account() {
	return(
		<div style={{'backgroundColor':'white','width':'100%','height':'790px'}}>
			<div id="login-form">
				
				<div>
					<div><input placeholder="Nhập username của bạn" id="username-field" type="text" name="username"/></div>
				</div>
				<div>
					<div><input placeholder="Nhập mật khẩu" id="password-field" type="password" name="password"/></div>
				</div>
				<div id="login-button">
				ĐĂNG NHẬP
				</div>
				<div id="border-line">
				</div>
				<div id="reg-button">
				ĐĂNG KÝ
				</div>
			</div>
			<div id="footer-login-page">
			</div>
		</div>
		)
}
export default Account;